let previewMode = 'popup';
const getPreviewMode = function () {
  if (previewMode === 'popup') {
    previewMode = 'embed';
  } else {
    previewMode = 'popup';
  }
  return previewMode;
};



const actions = {
  open: function () {
    open(function (project, File) {
      // 
      saveProject(project, File);
      onOpen(project);
    });
  },
  save: function () {
    set(project || '', function (File) {
      saveProject(project, File);
    });
  },
  saveAs: function () {
    set(
      project || '',
      function (File) {
        saveProject(project, File);
      },
      'saveAs'
    );
  },
  generate: function () {
    downloadFile('liam.html', getAppHtml());
  },
  toCodepen: function () {
    post('https://codepen.io/pen/define', {
      data: JSON.stringify({
        title: 'Liam Demo',
        description: 'Share From Liam Editor',
        tags: ['liam', 'react', 'json'],
        js_external: '', // 
        css_external: '', // 
        html: getDemoHtml(),
        js: '',
        editors: '100', // html/cssjs
      }),
    });
  },
};
