Liam.on('proect-actions',function(type){
  actions[type]();
});

import('mousetrap').then(function(js){
  const Mousetrap = js.default;
  const shortcuts = {
    open: 'mod+o',
    save: 'mod+s',
    saveAs: 'mod+shift+s',
  };
  for (const key in shortcuts) {
    if (Object.hasOwnProperty.call(shortcuts, key)) {
      const shortcut = shortcuts[key];
      Mousetrap.bind(shortcut, function() {
       actions[key]();
    });
    }
  }
});



window.addEventListener('beforeunload', function (event) {
  if (projectSaved !== project) {
    // 
    event.preventDefault();
    // ChromereturnValue
    event.returnValue = '';
  }
});

let firstLoad = true;

const LiamJSON = function (props) {
  project = props.project;
  onOpen = props.onOpen || function () {};
  onTogglePreviewMode = props.onTogglePreviewMode || function () {};
  if (firstLoad) {
    firstLoad = false;

    // 
    if(!window.opener){
      getFromLocal(function (p) {
        projectSaved = project = p;
        saveProject(p);
        onOpen(p);
      });
    }
    

    onTogglePreviewMode(getPreviewMode());
  } else {
    hasProjectChanged();
  }

  let json = [
    {
      type: 'a',
      props: {
        href: 'https://liam-js.github.io',
        target: '_blank',
        className: 'logo',
      },
      children: [
        {
          type: 'img',
          props: {
            src: process.env.PUBLIC_URL + '/logo.png'
          }
        },
        {
          type: 'span',
          children: 'LIAM'
        }
      ],
    },
  ];
  json = json.concat(
    buttons.map(function (item) {
      if (item === 'hr') {
        return {
          type: 'span',
          props: {
            className: 'divider-a',
          },
        };
      } else {
        return {
          type: 'button',
          children: item[1],
          props: {
            className: 'button-a',
            onClick: item[2],
          },
        };
      }
    })
  );
  return json;
};
export default LiamJSON;
